import CONF from './config'
import Api from '@/utils/api'

const houseInfoApi = CONF.houseInfo.API

export default {
  /**
   *[根据条件获取房屋信息列表]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getHouseInfo(params = {}) {
    const res = await Api.get(houseInfoApi['getHouseInfo'], params)
    return res
  },
  /**
   *[根据 proCollId 查询房屋基本信息]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getRoomInfo(params = {}) {
    const res = await Api.get(houseInfoApi['getRoomInfo'], params)
    return res
  },
  /**
   *[查询该房屋所有的调查走访核实记录]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getVisitInfo(params = {}) {
    const res = await Api.get(houseInfoApi['getVisitInfo'], params)
    return res
  },
  /**
   *[房屋生活信息--近一年用水趋势图]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getWaterTrend(params = {}) {
    const res = await Api.get(houseInfoApi['getWaterTrend'], params)
    return res.data
  },

  /**
   *[房屋生活信息--购物信息]
   * @param  {Object} params [description]
   * @return {[type]}        [description]
   */
  async getShoppingInfo(params = {}) {
    const res = await Api.get(houseInfoApi['getShoppingInfo'], params)
    return res.data
  },

  /**
   * 房屋基本信息保存
   * @param params
   * @returns {Promise<*>}
   */
  async saveBasicCommunity(params = {}) {
    const res = await Api.get(houseInfoApi['saveBasicCommunity'], params)
    return res.data
  }
}
