<template>
  <div class="power-manage">
    <div class="clearfix">
      <el-form :inline="true" :model="formInline" class="form-inline">
        <el-form-item label="小区名称">
          <el-input v-model="formInline.communityName" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item label="楼栋名称">
          <el-input v-model="formInline.apartmentNum" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item label="房号">
          <el-input v-model="formInline.roomNum" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item label="房屋状态">
          <el-select v-model="formInline.houseStatus" size="small" placeholder="请选择" clearable>
            <el-option
              v-for="item in options"
              :key="item.value"
              :label="item.label"
              :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="房东姓名">
          <el-input v-model="formInline.landlordName" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item label="房东身份证号">
          <el-input v-model="formInline.landlordId" size="small" placeholder="请输入" clearable></el-input>
        </el-form-item>
        <el-form-item label="房屋隐患">
          <el-select v-model="formInline.houseDanger" placeholder="请选择" clearable size="small">
            <el-option label="房屋存在隐患" value="1"/>
            <el-option label="房屋未存在隐患" value="2"/>
          </el-select>
        </el-form-item>
        <el-form-item>
          <el-button type="primary" size="small" @click="onQuery">查询</el-button>
        </el-form-item>
      </el-form>
    </div>
    <div style="text-align: right; margin-bottom: 10px;">
      <el-button size="small" v-if="isAdmin" type="warning" @click="uploadFun('register')">上传房屋登记信息</el-button>
      <el-button size="small" v-if="isAdmin" type="success" @click="uploadFun('trade')">上传房屋交易信息</el-button>
    </div>
    <div class="table-box" v-loading="isLoading">
      <el-table border :data="tableData">
        <el-table-column label="序号" type="index" :index="indexMethod" width="96" align="center"></el-table-column>
        <el-table-column prop="communityName" label="小区名称"></el-table-column>
        <el-table-column prop="apartmentNum" label="楼栋名称"></el-table-column>
        <el-table-column prop="houseUnit" label="单元" width="100px"></el-table-column>
        <el-table-column label="房号" width="80px">
          <template slot-scope="scope">
            <el-button type="text" @click="gotoRoomInfo(scope.row.proCollId)">{{ scope.row.roomNum }}</el-button>
          </template>
        </el-table-column>
        <el-table-column prop="houseStatus" label="房屋状态" width="80"></el-table-column>
        <el-table-column prop="houseSpecialMark" label="房屋特殊标记" width="120"></el-table-column>
        <el-table-column prop="houseDanger" label="房屋隐患情况" width="120"></el-table-column>
        <el-table-column label="房东信息">
          <template slot-scope="scope">
            <span>{{ scope.row.landlordName }} - {{ scope.row.landlordId }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="landlordPhone" label="联系方式"></el-table-column>
      </el-table>
      <el-pagination
        style="text-align: right; padding: 20px 0;"
        background
        layout="total, prev, pager, next"
        :current-page.sync="currentPage"
        :page-size="10"
        :total="total">
      </el-pagination>
    </div>
    <el-dialog
      :title="title"
      :visible.sync="dialogVisible"
      width="30%"
      @close="handleClose">
      <el-upload
        :action="uploadUrl"
        :limit="1"
        :on-success="onSuccess"
        :file-list="fileList">
        <el-button size="small" type="primary">点击上传</el-button>
        <div slot="tip" class="el-upload__tip">只能上传txt文件</div>
      </el-upload>
    </el-dialog>
  </div>
</template>

<script>
import Service from './service'
import Cookies from 'js-cookie'

export default {
  name: 'HouseInfo',
  data() {
    return {
      formInline: {
        communityName: '',
        apartmentNum: '',
        roomNum: '',
        houseStatus: '',
        landlordName: '',
        landlordId: '',
        houseDanger: ''
      },
      options: [
        {
          value: '自住',
          label: '自住'
        },
        {
          value: '出租',
          label: '出租'
        },
        {
          value: '空置',
          label: '空置'
        },
        {
          value: '待核',
          label: '待核'
        },
        {
          value: '其他',
          label: '其他'
        }
      ],
      tableData: [],
      total: 0,
      currentPage: 1,
      isLoading: false,
      officerNo: '',
      fileList: [],
      dialogVisible: false,
      title: '',
      uploadUrl: ''
    }
  },
  watch: {
    currentPage() {
      this.init(this.formInline)
    }
  },
  mounted() {
    this.init()
  },
  computed: {
    isAdmin() {
      let obj = JSON.parse(Cookies.get('officerInfo'))
      return obj.role === 'admin'
    }
  },
  methods: {
    async init(params = {}) {
      this.isLoading = true
      const defaultParams = {
        currentPage: this.currentPage,
        offset: 10
      }
      const res = await Service.getHouseInfo({ ...defaultParams, ...params })
      const { data, totalSize = 0 } = res
      this.tableData = data
      this.total = totalSize
      this.isLoading = false
    },
    onQuery() {
      this.currentPage = 1
      this.init(this.formInline)
    },
    indexMethod(index) {
      return (index + 1) + (this.currentPage - 1) * 10
    },
    gotoRoomInfo(id) {
      this.$router.push({
        path: '/room-info',
        query: {
          proCollId: id
        }
      })
    },
    uploadFun(name) {
      if (name === 'trade') {
        this.title = '上传房屋交易信息'
        this.uploadUrl = '/water/use/uploadHouseDealFile'
      } else {
        this.title = '上传房屋登记信息'
        this.uploadUrl = '/water/use/uploadHouseRegisterFile'
      }
      this.dialogVisible = true
    },
    handleClose() {
      this.dialogVisible = false
      this.fileList = []
    },
    onSuccess() {
      this.$message.success('文件上传成功！')
    }
  }
}
</script>

<style lang="less" scoped>
  .clearfix:after {
    content: "";
    display: block;
    clear: both;
  }

  .clearfix {
    zoom: 1;
  }
</style>
